@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@900&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgb(0, 0, 0);
}
#tsparticles {
	position: absolute;
	z-index: -1;
	width: 100vw;
	height: 100vh;
	top: 0px;
	display: flex;
}

* {
	margin: 0;
	padding: 0;
}

.app {
	display: flex;
	flex-direction: column;
	font-family: "Nunito Sans", sans-serif;
	height: 100vh;
	min-height: 100vh;
	width: 100vw;
	color: white;
	overflow: hidden;
}
.app__nav {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: space-between;
	height: 12vh;
	min-height: 11vh;
	max-height: 13vh;
}

.app__nav__image {
	margin-left: 2rem;
}
.app__nav__image img {
	height: auto;
	width: auto;
	max-height: 4vw;
}

.app__header > img {
	height: 2em;
	padding: 2em;
}

.app__router {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	min-height: 80%;
	min-width: 80%;
}
.nav__links {
	display: flex;
	align-self: center;
	margin-left: 10px;
	padding: 0.8vw;
	color: white;
	text-decoration: none;
}

.nav__links:focus {
	color: #fd5351;
	cursor: pointer;
}
@media only screen and (max-width: 600px) {
	.app__nav {
		width: 100%;
		font-size: 0.7em;
	}
}
.all_pages {
	width: 100%;
}
#tsparticles {
	position: absolute;
	z-index: -1;
	width: 100vw;
	height: 100vh;
	display: flex;
}

.home {
	display: flex;
	position: relative;
	flex-direction: column;
	width: 100%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding-left: 5vw;
	justify-content: left;
}

.home h1 {
	font-weight: 900;
	font-size: 8em;
	padding: 0;
	margin: 0;
	line-height: 14vh;
	word-break: break-all;
	scroll-behavior: smooth;
}
.home b {
	font-size: 10vw;
	color: #fd5351;
	word-break: break-all;
	line-height: 18vh;
}
@media only screen and (max-width: 600px) {
	.home h1 {
		font-size: 7em;
	}
}

.about {
	/* width: 90%; */
	position: relative;
	align-self: center;
	/* width: 100%; */
	padding: 0px 60px 0px 60px;
}
.about > h1 {
	font-weight: 900;
	font-size: 12vw;
	line-height: 14vh;
	word-break: break-all;
	scroll-behavior: smooth;
	color: #fd5351 !important;
	padding-bottom: 6vw;
}
.about > p {
	width: 70%;
	font-size: 1.2vw;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
	font-weight: 300;
	padding-bottom: 20px;
	width: 60%;
	word-break: break-word;
	text-align: justify;
}
@media only screen and (max-width: 600px) {
	.about > h1 {
		font-size: 13vw;
	}
	.about {
		width: 100%;
		padding: 15px;
	}
	.about > p {
		font-size: 12px;
		width: 90%;
	}
}

.contact {
	position: relative;
	width: 100%;
	padding: 0px 60px 0px 60px;
}
.contact > h1 {
	font-weight: 900;
	font-size: 13vw;
	line-height: 14vh;
	word-break: break-all;
	scroll-behavior: smooth;
	color: #fd5351 !important;
	padding-bottom: 6vw;
}
.contact > p {
	width: 70%;
	font-size: 1.2vw;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
	font-weight: 300;
	padding-bottom: 20px;
	width: 58%;
	word-break: break-word;
	text-align: justify;
}
@media only screen and (max-width: 600px) {
	.contact {
		width: 100%;
		padding: 15px;
	}
	.contact > p {
		font-size: 14px;
		width: 90%;
	}
}

.works {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: auto;
	width: 50vw;
	height: 80vh;
}
.work__card_list {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}
.slick-slider {
	position: relative;
	width: 100%;
}
@media only screen and (min-width: 601px , max-width: 1024px) {
	.works {
		/* width: 90%; */
		height: 100%;
		/* overflow: auto; */
	}
	.slick-slider {
		width: 950px;
	}
}
@media only screen and (max-width: 600px) {
	.works {
		/* width: 90%; */
		height: 100%;
		/* overflow: auto; */
	}
	.slick-slider {
		width: 350px;
	}
}

.workCard {
	position: relative;
	display: contents;
	align-items: center;
	justify-content: center;
	width: 100%;
}
.workCard > img {
	display: flex;
	width: 100%;
	object-fit: contain;
}

.social__medias {
	display: flex;
	position: absolute;
	width: 100%;
	z-index: 20;
	align-items: center;
	justify-content: center;
	bottom: 10px;
}

.social__medias a {
	margin-left: 10px;
	padding: 10px;
	color: white;
}
.social__medias a:hover {
	color: #fd5351;
}

.sideMenu {
	display: flex;
	flex-direction: row;
	width: auto;
	margin-right: 20px;
}

@media only screen and (max-width: 600px) {
	a.nav__links {
		font-size: 2vw;
	}
}

