.works {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: auto;
	width: 50vw;
	height: 80vh;
}
.work__card_list {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
}
.slick-slider {
	position: relative;
	width: 100%;
}
@media only screen and (min-width: 601px , max-width: 1024px) {
	.works {
		/* width: 90%; */
		height: 100%;
		/* overflow: auto; */
	}
	.slick-slider {
		width: 950px;
	}
}
@media only screen and (max-width: 600px) {
	.works {
		/* width: 90%; */
		height: 100%;
		/* overflow: auto; */
	}
	.slick-slider {
		width: 350px;
	}
}
