.contact {
	position: relative;
	width: 100%;
	padding: 0px 60px 0px 60px;
}
.contact > h1 {
	font-weight: 900;
	font-size: 13vw;
	line-height: 14vh;
	word-break: break-all;
	scroll-behavior: smooth;
	color: #fd5351 !important;
	padding-bottom: 6vw;
}
.contact > p {
	width: 70%;
	font-size: 1.2vw;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
	font-weight: 300;
	padding-bottom: 20px;
	width: 58%;
	word-break: break-word;
	text-align: justify;
}
@media only screen and (max-width: 600px) {
	.contact {
		width: 100%;
		padding: 15px;
	}
	.contact > p {
		font-size: 14px;
		width: 90%;
	}
}
