@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@900&display=swap");
* {
	margin: 0;
	padding: 0;
}

.app {
	display: flex;
	flex-direction: column;
	font-family: "Nunito Sans", sans-serif;
	height: 100vh;
	min-height: 100vh;
	width: 100vw;
	color: white;
	overflow: hidden;
}
.app__nav {
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: space-between;
	height: 12vh;
	min-height: 11vh;
	max-height: 13vh;
}

.app__nav__image {
	margin-left: 2rem;
}
.app__nav__image img {
	height: auto;
	width: auto;
	max-height: 4vw;
}

.app__header > img {
	height: 2em;
	padding: 2em;
}

.app__router {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	min-height: 80%;
	min-width: 80%;
}
.nav__links {
	display: flex;
	align-self: center;
	margin-left: 10px;
	padding: 0.8vw;
	color: white;
	text-decoration: none;
}

.nav__links:focus {
	color: #fd5351;
	cursor: pointer;
}
@media only screen and (max-width: 600px) {
	.app__nav {
		width: 100%;
		font-size: 0.7em;
	}
}
.all_pages {
	width: 100%;
}
#tsparticles {
	position: absolute;
	z-index: -1;
	width: 100vw;
	height: 100vh;
	display: flex;
}
