.about {
	/* width: 90%; */
	position: relative;
	align-self: center;
	/* width: 100%; */
	padding: 0px 60px 0px 60px;
}
.about > h1 {
	font-weight: 900;
	font-size: 12vw;
	line-height: 14vh;
	word-break: break-all;
	scroll-behavior: smooth;
	color: #fd5351 !important;
	padding-bottom: 6vw;
}
.about > p {
	width: 70%;
	font-size: 1.2vw;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
	font-weight: 300;
	padding-bottom: 20px;
	width: 60%;
	word-break: break-word;
	text-align: justify;
}
@media only screen and (max-width: 600px) {
	.about > h1 {
		font-size: 13vw;
	}
	.about {
		width: 100%;
		padding: 15px;
	}
	.about > p {
		font-size: 12px;
		width: 90%;
	}
}
